
#education-content {
    display: flex;
    grid-template-columns: 25% 70%;
    
    column-gap: 5%;

}

div#buttons {
    width: 100%;
    min-width: 250px;
    max-width: 350px;
    
}

div.education-tab {
    width: 100%;
    min-width: 500px;
}

div#buttons button {
    text-align: left;
    padding-left: 25px;
    width: 100%;
    max-height:50px;
    display:inline-block;
    margin:0px;
    border: none;
    color: var(--text);
    font-size: 1rem;
    border-left: 2px solid var(--text);
    border-radius: 0px;
}

div#buttons button.selected,div#buttons button:hover {
    color: var(--green);
    background-color: var(--light-navy);
    
}
div#buttons button.selected {
    border-color: var(--green);
}
div.education-tab div {
    padding-top: 10px 5px;
    height: 300px;
    line-height: 2.5ch;
}

div.education-tab div h3 {
    margin-top: 0px;
    color: var(--light-text);
    margin-bottom: 10px;
}
div.education-tab div p.duration {
    margin-top: 0px;
    color: var(--text);
}
div.education-tab div ul {
    color: var(--text);
    font-size: 1.5ch;
    line-height: 2.5ch;
}

ul#course-list {
    list-style-type: none;
}

@media screen and (max-width=600px) {
    #education-content {
        grid-template-columns: 50%
    }
    
}